.additionals {
  padding: 25px 20px 35px 20px;
}

.additionals__title {
  margin: 0 0 36px 0;

  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
}

.additionals__list {
  margin: 0;

  list-style-type: none;
}

.additionals__item {
  padding: 13px 0 19px 0;
}

.additionals__promo {
  position: relative;

  display: inline-block;
  margin: 0;
  padding: 187px 60px 43px 60px;

  font-size: 16px;
  line-height: 20px;
  font-family: $font-lato;
  text-align: center;
  color: $color-default-white;

  background-image: url("../img/promo/green-kitty.jpg");
  background-repeat: no-repeat;

  &::before {
    content: "";
    position: absolute;
    top: 59px;
    left: 50%;

    width: 78px;
    height: 80px;

    background-image: url("../img/promo/gift-icon.svg");
    background-repeat: none;
    transform: translateX(-50%);
  }
}
